<template>
  <div id="cn">
    <router-view></router-view>
    <button @click="changeLanguageEn">切换英文</button>
  </div>
</template>

<script>
  export default {
    name: "cnhome",
    methods:{
      changeLanguageEn() {
        let oldPath = this.$route.path
        let newPath = oldPath.replace('cn', 'en')
        const {href} = this.$router.resolve({
          path: newPath
        })
        window.open(href,'_blank')
      },
    }
  }
</script>

<style scoped>

</style>



