import Vue from 'vue'
import Router from 'vue-router'
// import HelloWorld from '@/components/HelloWorld'
import cc from '../../src/view/CnPages/home'
Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '*',
      redirect:'/cn/home/index',
    },
    {
      path:'/cn/home',
      name:"cnhome",
      component: () => import('../../src/view/CnPages/home'),
      children:[
        {
          path:'/cn/home/index',
          name:"cnindex",
          component: () => import('../../src/view/CnPages/index')
        }
      ]
    },
    {
      path:'/en/home',
      name:"enhome",
      component: () => import('../../src/view/EnPages/home'),
      children:[
        {
          path:'/en/home/index',
          name:"enindex",
          component: () => import('../../src/view/EnPages/index')
        }
      ]
    }
  ]
})



